export function boardWrapper(board: any, description: any) { // TODO: solve types with BoardProps<MyGameState>
  return ({ G, ctx, moves, log }: any) => {
    return (
      <div>
        <h1>boardgame.io Typescript Demo</h1>
        <p>{description}</p>
        <div>
          <button
            onClick={() => moves.chooseNewGameType("test")}
          >j prbajtk kezdse</button>
          <button
            onClick={() => moves.chooseNewGameType("live")}
          >j jtk kezdse</button>
        </div>
        <div>
          <button
            onClick={() => moves.chooseRole(0)}
          >Kezd leszek</button>
          <button
            onClick={() => moves.chooseRole(1)}
          >Msodik leszek</button>
        </div>
        {ctx.phase === 'startNewGame' && G.winner === null && <p> Kezdj j jtkot! </p>}
        {ctx.phase === 'chooseRole' && <p> Vlaszd ki, hogy els vagy msodik jtkos akarsz-e lenni. </p>}
        {ctx.phase === 'play' && ctx.currentPlayer === "0" && <p> Most Te jssz! </p>}
        {ctx.phase === 'play' && ctx.currentPlayer === "1" && <p> Vrakozs a szerverre... </p>}
        {ctx.phase === 'startNewGame' && G.winner === "0" && <p> Gratullok, nyertetek! </p>}
        {ctx.phase === 'startNewGame' && G.winner === "1" && <p> Sajnos a gp nyert. </p>}
        {ctx.gameover === true && G.points > 0 && <p> Gratullok, ktszer egymst utn megverttek a gpet. {G.points} pontot rtetek el ezen a forduln. </p>}
        {ctx.gameover === true && G.points === 0 && <p> Sajnos lejrt az idtk! :( </p>}
        {board({ G, ctx, moves })}
      </div>
    );
  };
};