import { MyGame } from './game';
import { MyBoard } from './board';
import { strategy } from './strategy';
import { ClientFactory } from '../../common/client_factory';

let description = <p className="text-justify">
  Ez a jtk a XIII. Drer dntjn szerepelt E kategriban.

  Kroly s Dezs m-ig szeretnnek elszmolni, s kzben a kvetkez jtkot jtsszk:
  0-rl kezdenek, a kt jtkos felvltva adhat hozz egy 13-nl kisebb pozitv egszet a korbbi
  szmhoz, azonban a babonjuk miatt ha egyikk x-et adott hozz, akkor msikuk a kvetkez
  lpsben nem adhat hozz 13  x-et. Az veszt, aki elri (vagy tlpi) m-et.

  Az j jtk gombra kattintva generlhatsz egy felllst.

  Az m szm ismeretben te dntheteted el, hogy a kezd vagy a msodik jtkos brbe szeretnl e bjni.
</p>

export const { Client, ClientWithBot } = ClientFactory(MyGame, MyBoard, strategy, description);
